.movies .movie{
    width: 520px;
    background-color: white;
    margin-bottom: 100px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px; 
    border-radius: 5px;
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6 px rgba(0, 0, 0, 0.025);
}
.movie img {
    position: relative;
    top: -50px;
    max-width: 120px;
    margin-right: 30px;
     
}
.movie .movie__title,
.movie .movie__year{
    margin: 0;
    font-weight: 300 ;
}

.movie .movie__year,
.movie .movie__genres{
    color: #b2b3bc;
    
}
.movie .movie__title{
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}
.movie .movie__genres{
    list-style: none;
    padding: 0;
    margin: 0;
    display: list-item;
    margin: 5px;    
}
.movie__genres li{
    margin-right: 10px;
    font-size: 14px;
    padding: 10px;
}