body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #eff3f7;
    height: 100%;
}
html,
body,
#potato,
.container{
    height: 100%;
    justify-self: center;
    display: flex;

}
.loader{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}
.movies{
    display: flex;
    justify-content: space-between;
    flex-wrap:wrap;
    padding: 50px;
    width:  60%;
}
